import React from 'react';


import Search from "./Search"

import Recent  from './recent';

import Result from "./result"
import { Component } from 'react/cjs/react.production.min';

import axios from 'axios';

class Weather extends Component  {

    constructor(props){
        super(props);
        this.state={

            lat:"",
            lon:"",

            city:"",

            weatherData:null,

            isSearch:false,

            recent:[],



        };
    }

    changehandler=(event)=>{

        const name=event.target.name;

        if(name==="city")
        {

            this.setState({
                city:event.target.value
            })
        }
        else
        {
            if(name==="lon")
            {

                this.setState({
                    lon:event.target.value
                })
            }
            else
            {
                if(name==="lat")
                {
                    this.setState({
                        lat:event.target.value
                    })
                }
            }
        }
        
    }

    //Search handler //////

    searchHandler=()=>{
        
        this.setState({
           

            isSearch:true,
        })

        axios.get(`http://api.openweathermap.org/data/2.5/weather?lat=${this.state.lat}&lon=${this.state.lon}&appid=599e0bbcb3b43b7f972e234af2b87aac`
                        
        )
        .then(
            (result)=>{

                this.setState({
                    city:result.data.name,

                    weatherData:result.data

                },
               ()=> this.recentHandler()
                )
                 
            }
        )

        .catch((error)=>{
            console.log(error);
        });

    }




    //{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{   Store data in Recent search }}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}

//(((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((())))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))

componentDidMount(){

    const data=window.localStorage.getItem("recent");

    const recent=data===null?[]:JSON.parse(data);
    this.setState({recent});

}



//{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{{}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}}
    //(((((((((((((((((((((((((((((((((((((  Recent Handler  )))))))))))))))))))))))))))))))))))))

recentHandler=()=>{

    let recent=this.state.recent;

    recent.push({
        lat:this.state.lat,

        lon:this.state.lon,

        city:this.state.city,


    })

    this.setState({recent},()=>{

        window.localStorage.setItem("recent",JSON.stringify(this.state.recent));

    })



}


//((((((((((((((((((((((((((((((((((((((((((   ReSearch Handler  ))))))))))))))))))))))))))))))))))))))))))


reSearchHandler=(lat,lon)=>{


    this.setState({weatherData:null},()=>{

        this.setState({lat,lon},()=>{




            axios.get(`http://api.openweathermap.org/data/2.5/weather?lat=${this.state.lat}&lon=${this.state.lon}&appid=599e0bbcb3b43b7f972e234af2b87aac`
                                
                )
                .then(
                    (result)=>{
        
                        this.setState({
                            city:result.data.name,
        
                            weatherData:result.data
        
                        },
                       
                        )
                         
                    }
                )
        
                .catch((error)=>{
                    console.log(error);
                });
        
        
        
        })
    })

}



//((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((     name search handler      ))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))












//(((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((((   Location Handler    )))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))))
    
    
    locationhandler=()=>{

        this.setState({
            lat:"",
            lon:"",
            city:"",
            weatherData:null,

            isSearch:true,
        })

        if(navigator.geolocation)
        {
            navigator.geolocation.getCurrentPosition(

                (res)=>{
                    setTimeout(() => {
                        
                        this.setState({
                            lat:res.coords.latitude,
     
                            lon:res.coords.longitude
                        })

                        axios.get(`http://api.openweathermap.org/data/2.5/weather?lat=${this.state.lat}&lon=${this.state.lon}&appid=599e0bbcb3b43b7f972e234af2b87aac`
                        
                            )
                            .then(
                                (result)=>{

                                    this.setState({
                                        city:result.data.name,

                                        weatherData:result.data

                                    },
                                    ()=> this.recentHandler())
                                     
                                }
                            )

                            .catch((error)=>{
                                console.log(error);
                            });
                   
                   
                        }, 500);
                   

                   

                },

                (error)=>{
                    console.log(error);
                }

            )
        }
        else
        {
            console.log("location is not supported ");
        }
    }
    render(){
    return(

        <div className="container pt-4  " style={{height:"600px",display:"flex"}}>
<Recent

recent={this.state.recent}

reSearch={this.reSearchHandler}



/>

            <div className="  ml-5 w-50%">

                
        <Search
        
        
        lat={this.state.lat}

        lon={this.state.lon}

        weatherData={this.state.weatherData}

        city={this.state.city}

        change={this.changehandler}

        getlocation={this.locationhandler}

        search={this.searchHandler}


        
        ></Search>
        
        <Result
        

        isSearch={this.state.isSearch}

        weatherData={this.state.weatherData}
        ></Result>
        

</div>
                </div>

        
);
}
}

export default Weather;
