import React from 'react';

export default function Recent(props){

    let data;

    console.log(props.recent);
    

    if(props.recent==null)
    {
        data="";
    }
    else
    {
        data=props.recent.map((recentData,id)=>{
            console.log(recentData);
            return <li key={id}   onClick={()=>props.reSearch(recentData.lat,recentData.lon)}  >{recentData.city}</li>;
        })
    }
    return(

    <div  className="card" style={{width:"15%",height:"80%",boxShadow:""}}>
            <h3>Recent</h3>
            <ul style={{backgroundColor:"dimgrey",color:"white",listStyleType:"none",cursor:"pointer"}}>{data}</ul>


            
        </div>
    )
}

