import React from 'react';

export default function Search(props){

    return(

        <div className="row  ml-5" style={{width:"85%",marginleft:"20%"}}>

            <div className="col-lg-5"> 
            
            <div className="form-group"> 

            <label htmlFor=""> Type city Name</label>

<input


type="text"
className="form-control"

name="city"

id="city"

value={props.city}

onChange={props.change}

aria-describedby="helpId"

placeholder=""

/>



               </div>
            </div>


            <div className="col-lg-1 text-center"> 

            <label htmlFor=""></label>

            <h5>Or</h5>



            </div>

            <div className="col-lg-5"> 

            <div className="form-group"> 

            <label htmlFor=""> Get Co-ordination</label>

            <button className="btn fa fa-crosshairs" onClick={props.getlocation} ></button>

           
            <div className="input-group flex-nowrap">

                <div className="bg-dark text-white rounded pt-1 pl-1 pr-1">Lat: 

            </div> 
            <input


type="number"

step="any"
className="form-control col mr-1"

name="lat"

value={props.lat}

onChange={props.change}

id="lat"
aria-describedby="helpId"

placeholder=""

/>
<div className="bg-dark text-white rounded pt-1 pl-1 pr-1" >Lon: 

            </div>
            <input


type="number"

step="any"
className="form-control col "

name="lon"

value={props.lon}

onChange={props.change}

id="lon"
aria-describedby="helpId"

placeholder=""

/>



            </div>




            </div>


            </div>

            <div className="col-lg-1"> 
            
            <label htmlFor="">Search</label>
            
            <button className="btn btn-primary fa fa-search"  onClick={props.search }></button>
            </div>
            </div>


        
    );
}
